/* Sidebar Styles */
.sidebar {
  background-color: #1f1f2f;
  color: white;
  width: 250px;
  height: calc(100vh - 60px); /* Adjust for the header height */
  position: fixed;
  top: 70px; /* Header height */
  left: 0;
  transition: width 0.3s ease, top 0.3s ease; /* Smooth transition for changes */
  display: flex;
  flex-direction: column;
  overflow: hidden; /* Prevents content overflow in collapsed state */
  z-index: 1010; /* Ensure it is below the header (header z-index > sidebar) */
}

.sidebar.collapsed {
  width: 80px;
}

.navList {
  list-style: none;
  padding: 0;
  margin: 0;
}

.navItem {
  margin: 0;
}

.navLink {
  display: flex;
  align-items: center;
  padding: 15px;
  text-decoration: none;
  color: white;
  transition: background-color 0.3s ease;
}

.navLink:hover {
  background-color: #a5152a;
}

.navLink span {
  margin-left: 10px;
}

.sidebar.collapsed .navLink span {
  display: none;
}

.navItem.active .navLink {
  background-color: #ff6b81;
  color: white;
}



/* Responsive Design */
@media (max-width: 1024px) {
  .sidebar {
    width: 80px; /* Default collapsed width for medium screens */
  }

  .sidebar.collapsed {
    display: flex;
    width: 60px; /* Adjust collapsed sidebar width for smaller devices */
  }

  .navLink {
    padding: 12px;
  }
}

@media (max-width: 1024px) {
  .sidebar {
    display: none; /* Hide sidebar completely on very small screens */
  }

  .sidebar.collapsed {
    display: flex; /* Allow collapsed state visibility */
    width: 60px;
  }

  .navLink {
    padding: 10px;
    font-size: 14px;
  }

  .navLink span {
    font-size: 12px;
  }
}
